import { HashRouter as Router, Routes, Route, useParams, Link } from 'react-router-dom';
import Home from './pages/Home';
import Map from './pages/Map';
import Changelog from './pages/Changelog';
import PlayerSearch from './pages/PlayerSearch';
import PlayerInfo from './pages/PlayerInfo';
import { MapProvider } from './MapContext';
import {
  QueryClient,
  QueryClientProvider,
} from '@tanstack/react-query';
import './App.css';

const queryClient = new QueryClient();

export default function App() {
  return (
    <QueryClientProvider client={queryClient}>
      <MapProvider>
        <Router>
          <div className="NavBar">
            <Link to={{pathname: "/"}}>Home</Link>
            <Link to={{pathname: "/changelog"}}>Notes</Link>
            <Link to={{pathname: "/player/search"}}>Players</Link>
            <a href="https://github.com/vv4t/Aottg2-Racing-Maps/" target="_blank">Github</a>
            <a href="https://woksan.uk/discord" target="_blank">Discord</a>
          </div>
          <Routes>
            <Route path="/" element={<Home/>} />
            <Route path="/changelog" element={<Changelog/>} />
            <Route path="/map/:mapId" element={<Map/>} />
            <Route path="/player/search" element={<PlayerSearch/>} />
            <Route path="/player/info/:profileId" element={<PlayerInfo/>} />
          </Routes>
        </Router>
      </MapProvider>
    </QueryClientProvider>
  );
}

