import { createContext, ReactNode } from "react";
import { useQuery } from '@tanstack/react-query';

interface MapContextProps {
  children?: ReactNode
}

export interface MapInfo {
  id: string,
  author: string,
  name: string,
  difficulty: number,
  tags: string,
  notes: string,
}

export const MapContext = createContext<{ [id: string]: MapInfo}>({});

export function MapProvider({ children }: MapContextProps) {
  const { isPending, error, data } = useQuery({
    queryKey: ['map'],
    queryFn: () => (
      fetch("/Aottg2-Racing-Maps/maps.csv")
        .then((response) => response.text())
        .then((csv) => {
          const csvRows = csv
            .split("\n")
            .filter((x) => x.trim() !== "")
            .map((x) => x.split("|"))
            .slice(1);
          
          return Object.fromEntries(csvRows.map(row => [row[0], {
            id: row[0],
            author: row[1],
            name: row[2],
            difficulty: parseFloat(row[3]),
            tags: row[4],
            notes: row[5],
          }]));
        })
    ),
  });

  return <MapContext.Provider value={data || {}}>{children}</MapContext.Provider>;
}
