import { useEffect, useContext } from 'react';
import { useNavigate, useParams, Link } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { MapContext } from '../MapContext';
import { toStarRating } from '../util';
import { API_URL } from "../constant";
import './Map.css';

export default function Map() {
  const params = useParams();
  const navigate = useNavigate();
  const maps = useContext(MapContext);

  const map = params.mapId && maps[parseInt(params.mapId)];
  const starRating = map ? toStarRating(map.difficulty) : '';
  const base = map ? `${map.author} - ${map.name} ${starRating}` : '';
  const image = map ? `/Aottg2-Racing-Maps/images/${params.mapId}.jpg` : '/missing.png';
  const download = map ? `/Aottg2-Racing-Maps/maps/${base}.txt` : '';
  
  const rankings = useQuery({
    queryKey: ['ranking_all:' + params.mapId],
    queryFn: () => (
      fetch(`${API_URL}/api/racing/ranking_all/${encodeURIComponent(params.mapId as string)}`)
        .then((response) => response.json())
    ),
  });

  const rankingDisplay = !rankings.isPending && rankings.data.entries().map((ranking: [ number, string[] ]) => {
    const [rank, info] = ranking;
    return (
      <tr>
        <td className="RankPlace">{rank + 1}</td>
        <td className="RankName"><Link to={{pathname: `/player/info/${encodeURIComponent(info[0])}`, search: `?name=${encodeURIComponent(info[1])}`}}>{info[1]}</Link></td>
        <td className="RankTime">{info[2]}</td>
      </tr>
    );
  });
  
  return map ? (
    <div className="Map">
      <h1>{map.name} - {map.author}</h1>
      <h2>{starRating}</h2>
      <div className="Navigate">
        <a href={download} target="_blank">Download</a>
      </div>
      <div className="MapPreview">
        <div className="MapImage">
          <img src={image} onError={e => (e.target as HTMLImageElement).src = '/missing.png'}/>
        </div>
      </div>
      <div className="Rank">
        <table>
          {!rankings.isPending && rankings.data.length > 0 ? <tbody>{rankingDisplay}</tbody> : "No submitted runs."}
        </table>
      </div>
    </div>
  ) : (
    <h1>Loading...</h1>
  );
}
