import { useEffect, useState, useContext, useRef } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { MapContext } from '../MapContext';
import { API_URL } from "../constant";

interface PlayerMatch {
  id: string,
  name: string,
}

export default function PlayerSearch() {
  const navigate = useNavigate();
  const maps = useContext(MapContext);
  
  const [query, setQuery] = useState<string>("");
  const [matches, setMatches] = useState<PlayerMatch[]>([]);
  const debounce = useRef<ReturnType<typeof setTimeout> | undefined>(undefined);

  const handleQuery = async (query: string) => {
    if (query !== "") {
      const response = await fetch(`${API_URL}/api/racing/player/search?q=${encodeURIComponent(query)}`);
      const data = await response.json();
      setMatches(data as PlayerMatch[]);
    } else {
      setMatches([]);
    }
  };

  const onQueryChange = (query: string) => {
    if (debounce.current)
      clearTimeout(debounce.current);
    debounce.current = setTimeout(() => handleQuery(query), 500);
  };

  const playerDisplay = Array.from(matches.entries()).map((match: [number, PlayerMatch]) => (
    <p>
      <Link to={{pathname: `/player/info/${encodeURIComponent(match[1].id)}`, search: `?name=${encodeURIComponent(match[1].name)}`}}>
        {(match[0] + 1) + ". " + match[1].name}
      </Link>
    </p>
  ));

  return (
    <div className="Home">
      <h1>Player Search</h1>
      <div className="Search">
        <input type="text" placeholder="Search..." onChange={e => onQueryChange(e.target.value)} />
      </div>
      {playerDisplay}
    </div>
  );
}
