import { useEffect, useState, useContext, ReactNode } from 'react';
import { useNavigate, useParams, Link, useSearchParams } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { MapContext } from '../MapContext';
import { toStarRating } from '../util';
import { API_URL } from "../constant";

export default function PlayerInfo() {
  const params = useParams();
  const navigate = useNavigate();
  const maps = useContext(MapContext);

  const [searchParams, setSearchParams] = useSearchParams();

  const { isPending, data } = useQuery({
    queryKey: ['player:' + params.mapId],
    queryFn: () => (
      fetch(`${API_URL}/api/racing/player/info/${encodeURIComponent(params.profileId as string)}`)
        .then((response) => response.json())
    ),
  });
  
  const runDisplay: ReactNode[] = [];
  
  const addRun = (mapId: number, time: null | number) => {
    const map = maps[mapId];

    if (!map)
      return;

    const rowStyle = {
      textAlign: "left" as const,
      color: time ? "#99ff99" : "#ff9999",
    };

    runDisplay.push(
      <tr style={rowStyle} key={map.id}>
        <td><Link to={{pathname:`/map/${map.id}`}}>{map.name} - {map.author}</Link></td>
        <td>{toStarRating(map.difficulty)}</td>
        <td>{time ? time as number : "None"}</td>
      </tr>
    );
  };

  const completionCount = data ? (data[0].length) : 0;
  const totalCount = Object.values(maps).length;

  if (data) {
    let i = 0;
    let j = 0;
    let nextMapId = data[0][j];

    for (let i = 0; i < totalCount; i++) {
      if (i == nextMapId) {
        addRun(i, data[1][j]);
        nextMapId += data[0][++j];
      } else {
        addRun(i, null);
      }
    }
  }

  return !isPending ? (
    <div className="Map">
      <h1>{searchParams.get("name")}</h1>
      <div className="Rank">
        <p>Completed {completionCount}/{totalCount} maps.</p>
        {data[0].length > 0 ? <table><tbody>{runDisplay}</tbody></table> : "No submitted runs."}
      </div>
    </div>
  ) : (
    <h1>Loading...</h1>
  );
}
