import { useEffect, useState, useContext, useRef } from 'react';
import { Link } from 'react-router-dom';
import { MapInfo, MapContext } from '../MapContext';
import { toStarRating } from '../util';
import './Home.css';

export default function Home() {
  console.log(process.env.API_URL);

  const [chunks, setChunks] = useState<MapInfo[][]>([]);
  const debounce = useRef<ReturnType<typeof setTimeout> | undefined>(undefined);

  const maps = useContext(MapContext);

  const refreshMaps = (query: string) => {
    const filteredMaps = Array.from(
      Object.entries(maps).filter(([ id, info ]) => (
        query === ""
          || info.author.toLowerCase().includes(query.toLowerCase())
          || info.name.toLowerCase().includes(query.toLowerCase())
      ))
      .map(x => x[1])
    );

    filteredMaps.sort((a, b) => (a.author + a.name).localeCompare(b.author + b.name));

    const chunks = filteredMaps.reduce((chunks: MapInfo[][], _, i) => {
      if (i % size === 0)
        chunks.push(filteredMaps.slice(i, i + size));
      return chunks;
    }, []);

    setChunks(chunks);
  };

  const onQueryChange = (query: string) => {
    if (debounce.current)
      clearTimeout(debounce.current);
    debounce.current = setTimeout(() => refreshMaps(query), 300);
  };

  useEffect(() => {
    refreshMaps("")
  }, [maps]);

  const size = 5;

  const mapDisplay = chunks.map((row) => {
    const padding = new Array(size - row.length).fill(<td/>);

    return <tr>
      {row.map((map) => {
        const starRating = toStarRating(map.difficulty);
        const title = `${map.author} - ${map.name}`;
        const image = `/Aottg2-Racing-Maps/images/${map.id}.jpg`;

        return <td key={map.id}>
          <Link style={{textDecoration: "none"}} to={{pathname: `/map/${map.id}`}}>
            <div className="MapInfo">
              <div className="MapImage">
                <img src={image} loading="lazy" onError={e => (e.target as HTMLImageElement).src = '/missing.png'}/>
              </div>
              <div className="MapTitle">{title}</div>
              <div className="MapDifficulty">{starRating}</div>
            </div>
          </Link>
        </td>;
      })}
      {padding}
    </tr>;
  });

  return (
    <div className="Home">
      <h1>Tofu Delivery - Aottg 2</h1>
      <p>24/7 hosted Asia Racing</p>
      <p>Hosted by wok | Map collection managed by Fruity and Riblei</p>
      <div className="Search">
        <input type="text" placeholder="Search..." onChange={(e) => onQueryChange(e.target.value)} />
      </div>
      <div className="MapContainer">
        <table>
          <tbody>
            {mapDisplay}
          </tbody>
        </table>
      </div>
    </div>
  );
}
